@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@1,700&family=Noto+Sans+Mongolian&family=Noto+Serif+NP+Hmong&display=swap');

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;
    background-color: rgba(0, 0, 0, 0.75);
}

/* ----------------------------------- */


.modal {
    background-color: white;
    position: fixed;
    z-index: 21;
    width: 1000px;
    height: 550px;
    top: 12%;
    left: 15%;
    border-radius: 10px;
}

.modal .row {
    overflow: hidden;
}

.modal .row .col {
    height: 550px;
}

.modal img {
    width: 100%;
    height: 550px;
}

/* --------------------------------------- */
.infoProduct {
    font-family: 'Noto Sans Mongolian', sans-serif;
    text-align: justify;
    padding: 30px 50px 50px 20px;
    line-height: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.infoProduct h1 {
    line-height: 40px;
    margin: 0;
    font-size: 20px;
}

.infoProduct .price {
    margin: 5px 0;
    font-size: 14px;
    font-weight: 600;
    color: rgb(86, 86, 86);
}

.infoProduct .description {
    height: 300px;
    margin: 5px 0;
    color: rgb(145, 145, 145);
    font-size: 13px;
}

.infoProduct button {
    align-self: flex-end;
    width: 200px;
    margin-top: 30px;
    border-radius: 5px;
    padding: 10px 20px;
    background-color: #383838;
    color: white;
    border: 1px solid transparent;
    font-style: italic;
    letter-spacing: 1px;
    font-size: 16px;
    cursor: pointer;
}

/* Style: Effect */

.infoProduct button:hover {
    transition: 0.3s all ease;
    background-color: #1480df;
    color: white;
    border: 1px solid transparent;
    border-radius: 0;
}

/* Style: Icon */
.iconClose {
    border-top-right-radius: 5px;
    border-bottom-left-radius: 100%;
    line-height: 30px;
    display: block;
    background-color: #383838;
    width: 50px;
    height: 50px;
    color: white;
    font-size: 20px;
    font-weight: bold;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;

}

.iconClose:hover {
    transition: 0.3s all ease-in;
    color: #383838;
    background-color: white;

}

.iconClose svg {
    position: absolute;
    top: 10px;
    left: 20px;
}

/* ------------Modal Mess---------------- */
.modalMess {
    background-color: red;
    width: 200px;
    height: 200px;
}