@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');

.container {
    margin-left: 5%;
    font-style: italic;
    font-family: 'Noto Sans Mongolian', sans-serif;
    width: 50%;
}

.description {
    border: 1px solid #353535;
    border-radius: 2px;
    color: white;
    background-color: #353535;
    line-height: 0px;
    font-size: 18px;
    padding: 30px;
    margin-top: 50px;
    font-style: italic;
    letter-spacing: 1px;

}

.container .title {
    font-weight: 400;
    color: #353535;
    font-size: 24px;
    letter-spacing: 1px;
    margin: 20px 0;
}

.container .ul {
    padding-left: 0;
}

.container .ul li:not(li:first-child) {
    margin-left: 40px !important;
}

.titleDesc {
    margin: 10px 0;
    font-size: 16px;
    color: gray;
    line-height: 35px;
}

.titleDesc:first-child {
    position: relative;
    left: 0;
    list-style: none;
    text-transform: uppercase;
    font-size: 20px;
    font-style: normal;
    color: gray;
    padding: 0 !important;
}

.titleDesc:not(:first-child) {
    margin-left: 20px;
    font-style: italic;
}

/* Style: effect */
.description:hover {
    border: 1px solid transparent;
}