.img {
    width: 100%;
    height: 500px;
    border-radius: 5px;
    box-shadow: 2px 2px 2px 2px lightgray, -2px -2px 2px 2px rgb(128, 128, 128);
}

.anotherImg {
    margin-top: 20px;
    width: 100px;
    height: 100px;
    cursor: pointer;
}

/* Style: effect */
.anotherImg:hover {
    transition: 0.5s all ease;
    transform: scale(1.1);
}