@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Old+Uyghur&display=swap');

.order {
    background-color: #F8F9FA;
    font-style: italic;
    margin-top: 80px;
}

.container {
    width: 70%;
    margin: 0 auto;
    padding: 30px 0;
}

.title {
    font-family: 'Noto Serif Old Uyghur', serif;
    margin-bottom: 20px;
}

.userOrder {
    border-bottom: 1px solid black;
    padding-bottom: 10px;
    align-items: flex-start;
    margin: 20px 0;
}


.userOrder p.name {
    font-family: 'Noto Sans Mongolian';
    font-size: 16px;
    color: rgb(78, 77, 77);
    font-weight: 700;
    margin: 0;
}

.userOrder p.price {
    color: rgb(155, 155, 155) !important;
    font-weight: 500;
    display: inline-block;
    font-size: 16px;
    margin-top: 5px;

}

.userOrder p.price~span {
    color: rgb(155, 155, 155) !important;
    font-weight: 500;
    margin-left: 10px;
}

/* -------------------------------------- */

.total {

    align-items: center;
}

.total p {
    font-family: 'Noto Sans Mongolian', sans-serif !important;
    font-size: 20px;
    color: rgb(78, 77, 77);
    font-weight: 700;
}

.total p.price {
    color: rgb(91, 91, 91) !important;
    font-weight: 500;
    font-size: 24px;
    text-align: right;
}