@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Old+Uyghur&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');

.products {
    text-align: center;
    margin: 0 0 50px;
}

.products p,
.products h2 {
    margin: 0;
    font-style: italic;
    font-family: 'Noto Serif Old Uyghur', serif;
}

.products p {
    color: #b1b1b1;
}

.products h2 {
    color: #484848;
}

.products .row {
    margin-top: 20px;
}

.products .col {
    margin: 20px 0px;
    text-align: center;
}

.products .row img {
    width: 270px;
    height: 250px;
    object-fit: cover;
}

.products .col p {
    color: rgb(56, 56, 56);

}

.products .col .name {
    margin-top: 10px;
    font-weight: 600;
    font-size: 14px;
    font-family: 'Noto Sans Mongolian', sans-serif;
    padding: 0 40px;

}

.products .col .price {
    color: #bdc1c4;
    font-size: 14px;
    font-family: 'Noto Sans Mongolian', sans-serif;

}

/* Style: Effect */
.products .row img:hover {
    cursor: pointer;
    box-shadow: 4px 4px 2px 0 gainsboro;
    transition: 0.3s all ease-in;
}

