@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');

.modal {
  position: fixed;
  bottom: 5%;
  right: 2%;
  z-index: 1;

}

.modal .iconMess {
  cursor: pointer;
  font-size: 30px;
  color: white;
  border: 3px solid rgb(74, 74, 74);
  border-radius: 100%;
  padding: 8px;
  background-color: black;
}

.iconMess:hover {
  transition: 0.5s all ease;
  border: 3px solid rgb(35, 172, 236);
  color: rgb(35, 172, 236);
  background-color: white;
}

/* -------------Open Mess------------------- */
.openModalChat {
  position: relative;
  top: -20px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 0 5px 2px rgb(185, 184, 184);
  width: 400px;
  height: 400px;
  animation-name: openModalChat;
  animation-duration: 1s;
  animation-iteration-count: 1;
}

.header {
  padding: 0 20px;
  border-bottom: 1px solid rgb(197, 196, 196);
}



.header .headerTitle p {
  font-family: 'Noto Sans Mongolian', sans-serif;
  font-weight: 700;
  color: rgb(67, 66, 66);
  font-size: 16px;
  margin: 10px 0;
}

.header .iconClose {
  text-align: right;
  font-size: 20px;
}

.header .iconClose:hover {
  color: rgb(244, 71, 71);
}

/* ------------------------------------- */
.contentBox {
  padding: 0 20px;
}

.contentAdmin {
  margin-top: 20px;
  text-align: right;
}

.contentAdmin p {
  font-family: 'Noto Sans Mongolian', sans-serif;
  background-color: #48B0F7;
  color: white;
  display: inline-block;
  padding: 5px 20px;
  margin: 5px 0;
}

.contentUser {
  margin-top: 20px;
}

.contentUser p {
  font-family: 'Noto Sans Mongolian', sans-serif;
  background-color: #F5F6F7;
  color: black;
  display: inline-block;
  padding: 5px 20px;
  margin: 5px 0;
}

.footer {
  margin-top: 33%;
  background-color: #FFFFFF;
  border-top: 1px solid lightgray;
  align-items: center;
  height: 45px;
  padding: 0 20px;
}

.inputMess {
  margin-left: 10px;
  width: 80%;
  border: 1px solid gray;
  border-radius: 3px;
  padding: 5px 10px;
}

.inputMess:focus {
  outline: 0;
}

.icon {
  text-align: right;
}

@keyframes openModalChat {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}