@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@1,700&family=Noto+Sans+Mongolian&family=Noto+Serif+NP+Hmong&display=swap');

.banner {
    background-color: #F6F9F6;
    padding: 50px 5%;
    font-style: italic;
    font-family: 'Noto Sans Mongolian', sans-serif;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
    height: 200px;
}

.banner h1 {
    color: rgb(50, 50, 50);
    font-weight: 400;
    font-size: 44px;
}

.banner p {
    color: gray;
    font-size: 20px;
}

.breadcrumb {
    font-size: 24px;
}

.pageHome,
.pageCart {
    color: rgb(50, 50, 50) !important;
}

.pageHome,
.pageCart,
.pageCheckout:hover {
    background-color: transparent !important;
}