@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@1,700&family=Noto+Sans+Mongolian&family=Noto+Serif+NP+Hmong&display=swap');


/* Style: footer */
.footer {
    background-color: #111111ec;
    color: white;
    padding: 60px 80px;
    font-style: italic;
    text-align: center;
}

/* Style: row */
.container {
    width: 1140px;
    margin: 0 auto;
}

/* Style: listLink */
.footer p {
    margin-bottom: 30px;
    font-size: 18px;
    font-family: 'Noto Sans Mongolian', sans-serif;
}

.footer a {
    display: block;
    margin: 10px 0;
    color: gray;
    font-family: 'Noto Sans Mongolian', sans-serif;
}

/* Style: effect */
.footer a:hover {
    color: rgb(232, 149, 16);

}

@media screen and (min-width:320px) and (max-width:575px) {
    .footer {
        padding: 20px;
    }

    .container {
        flex-direction: column;
        width: 100%;
        align-items: center;
    }
}


@media screen and (min-width:576px) and (max-width:767px) {
    .footer {
        padding: 20px;
    }

    .container {
        width: 100%;
        justify-content: space-between !important;
    }
}


@media screen and (min-width:768px) and (max-width:991px) {
    .footer {
        padding: 20px;
    }

    .container {
        width: 100%;
        justify-content: space-between !important;
    }
}


@media screen and (min-width:992px) {
    .footer {
        padding: 20px;
    }

    .container {
        width: 100%;
        justify-content: space-around !important;
    }
}