@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');

.formBill {
    margin: 20px 0 10% 0;
    font-family: 'Noto Serif Old Uyghur', serif;
}

.formBill .title {
    font-style: italic;
}

.form {
    margin-top: 30px;
}

.form div {
    margin: 30px 0;
}

.form label {
    font-style: italic;
    color: rgb(153, 153, 153);
    letter-spacing: 1px;
    font-weight: 600;
}

.form .input {
    font-family: 'Noto Serif Old Uyghur', serif;
    width: 100%;
    padding: 15px;
    margin-top: 10px;
    border: 1px solid gray;
}

.form .input::placeholder {
    font-family: 'Noto Serif Old Uyghur', serif;
}

.form .btnOrder {
    font-family: 'Noto Serif Old Uyghur', serif;
    cursor: pointer;
    border: 1px solid #353535;
    border-radius: 0;
    color: white;
    background-color: #353535;
    font-style: italic;
    padding: 20px 50px;
    line-height: 10px;
    font-size: 18px;
}

/* -------------Error Message--------------------- */
.inputInvalid {
    font-family: 'Noto Serif Old Uyghur', serif;
    width: 100%;
    padding: 15px;
    margin-top: 10px;
    border: 1px solid red;
}

.errorMessage {
    font-family: 'Roboto Slab', serif;
    color: red;
    font-style: italic;
    font-size: 14px;
    margin: 5px 0;
}

/* Style: Effect CSS  */
.form input:focus {
    outline: 1px solid rgb(166, 164, 164);
    border: 1px solid transparent;
}