@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital@0;1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Old+Uyghur&display=swap');

.container {
    padding: 0;
    margin: 0;
    font-family: 'Merriweather', serif;
    font-weight: 200;
    margin: 0 auto;
    transform: translateY(10%);
    width: 35%;
    height: 600px;
    background-color: white;
    border-radius: 10px;
}

.container h1 {
    text-align: center;
    padding: 50px 0;
    color: #494949;
    font-style: italic;
}

.form {
    width: 100%;
    text-align: center;
}

.form .row {
    position: relative;
}


.form .row .icon {
    position: absolute;
    font-size: 18px;
    left: 15%;
    top: 25px;
}

.btn {
    width: 70%;
    border-radius: 0;
    padding: 30px 0;
    line-height: 0;
    margin: 20px 0 30px 0;
    background-color: #494949;
    color: rgb(230, 229, 229);
    letter-spacing: 1px;
    font-family: 'Merriweather', serif;
}

.login {
    font-family: 'Noto Serif Old Uyghur', serif;
    font-style: italic;
}

.login .a {
    font-family: 'Noto Serif Old Uyghur', serif;
    text-decoration: none;
    color: rgb(37, 123, 209);
    margin-left: 3px;
}

/* Style: effect */
.container h1:hover {
    transition: 0.3s all ease;
    color: rgb(25, 170, 222);
}

.form .row:hover>.icon {
    transition: 0.5s all ease;
    color: rgb(25, 170, 222);
}

.btn:hover {
    background-color: rgb(25, 170, 222) !important;
    transition: 0.5s all ease-in-out;
}