@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');

/* Reset CSS */
.container .title,
.nameLogo a {
    display: none;
}

/* Style: navbar */
.navbar {
    padding: 15px 30px;
    margin: 0;
    list-style: none;
}

/* Style: Col */
.colRight {
    text-align: right;
}

.colCenter {
    text-align: center;
}

.colRight ul li:nth-child(1) a {
    position: relative;
    margin-left: 15px;
}

.colRight ul li:nth-child(1) .icon {
    margin-right: 5px;
}

.colRight ul li:nth-child(1) .quantity {
    position: absolute;
    color: white;
    left: 0px;
    top: 0px;
    padding: 0px 4px;
    border-radius: 100%;
    font-size: 10px;
    background-color: rgb(234, 68, 68);
    margin-left: 10px;
    font-family: 'Roboto Slab', serif;
}

/* Style: Link */
.navbar ul {
    padding: 0;
}

.navbar ul li {
    display: inline-block;
    margin: 0 10px;
}

.navbar a {
    color: rgb(60, 60, 60);
    font-family: 'Cormorant Garamond', serif;
    font-size: 22px;
}

.colCenter ul li a {
    font-size: 30px;
    letter-spacing: 1px;

}

.logout {
    margin-left: 15px;
    font-size: 22px;
    font-family: 'Cormorant Garamond', serif;
    cursor: pointer;
}

.icon {
    margin-right: 3px;
}

/* Style: effect */
.navbar ul li :hover {
    color: rgb(232, 149, 16);
}

.active {
    color: rgb(232, 149, 16) !important;
}


@media screen and (min-width:320px) and (max-width:575px) {
    .navbar {
        padding: 0;
        background-color: #f4f5f9;
    }

    .colCenter {
        order: 3;
    }

    .colRight li {
        margin: 0 !important;
    }

    .colRight .cart {
        margin: 0 !important;
    }

    .colRight .logout {
        margin: 0 5px 0 10px;
    }

    .colCenter {
        background-color: white;
    }

    .userLogin {
        display: none;
    }
}


@media screen and (min-width:576px) and (max-width:767px) {
    .navbar {
        padding: 10px 5px;
    }

    .userLogin {
        display: none;
    }

    .colRight li {
        margin: 0 !important;
    }

    .colRight .cart {
        margin: 0 !important;
    }

    .colRight .logout {
        margin: 0 5px 0 10px;
    }
}


@media screen and (min-width:768px) and (max-width:991px) {
    .navbar {
        padding: 20px 5px;
    }

    .colRight li {
        margin: 0 5px !important;
    }

    .colRight .cart {
        margin: 0 !important;
    }

    .colRight .logout {
        margin: 0 5px 0 15px;
    }
}


@media screen and (min-width:992px) {}