@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Old+Uyghur&display=swap');

.banner {
    position: relative;
    background: url(../../imgs/banner1.jpg) center no-repeat;
    width: 100%;
    height: 500px;
}

.infoBanner {
    font-family: 'Noto Serif Old Uyghur', serif;
    display: inline-block;
    width: 350px;
    position: absolute;
    top: 30%;
    left: 7%;
    font-style: italic;
}

.infoBanner .title {
    color: rgb(142, 142, 142);
    margin: 0;
}

.infoBanner .discountContent {
    margin: 10px 0;
    line-height: 40px;
    color: rgb(20, 32, 47);
    font-weight: 500;
}

.infoBanner .btnTransfer {
    color: #9f9e9e;
    background-color: #383838;
    font-style: italic;
    font-size: 18px;
    border-radius: 2px;
    border: 2px solid transparent;
    padding: 10px 30px;
    margin-top: 10px;
    cursor: pointer;
}

/* Style: effect */

.infoBanner button:hover {
    transition: 0.5s all ease-in-out;
    border: 2px solid black;
    background-color: transparent;
    color: #383838;
}




@media screen and (min-width:320px) and (max-width:575px) {
    .banner {
        width: 100%;
    }

    .infoBanner {
        position: relative;
        width: 100%;
        left: 0;
        text-align: center;
    }

    .infoBanner .discountContent {
        font-size: 28px;
    }
}


