@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Old+Uyghur&display=swap');


.categories {
    text-align: center;
    width: 100%;
    margin: 50px auto 20px;
}

.categories p,
.categories h2 {
    margin: 0;
    font-style: italic;
    font-family: 'Noto Serif Old Uyghur', serif;
}

.categories p {
    color: #b1b1b1;
}

.categories h2 {
    color: #484848;
}

.categories .row {
    margin: 0 auto;
    width: 90%;
    margin-top: 20px;
    justify-content: center;
}

.categories img {
    margin: 15px;
    object-fit: cover;
    cursor: pointer;
}

.categories .row .col:nth-child(4) img {
    margin: 15px 25px;
}

/* Style: effect  */
.categories img:hover {
    filter: brightness(103%);
}


@media screen and (min-width:320px) and (max-width:575px) {
    .col {
        width: 100%;
        padding: 20px;
    }

    .categories img {
        width: 100%;
        margin: 10px 0;
        height: 300px;
    }

    .categories .row .col:nth-child(4) img {
        margin: 0;
    }
}


@media screen and (min-width:576px) and (max-width:767px) {
    .col {
        width: 50%;
        padding: 0 20px;
    }

    .categories img {
        width: 100%;
        margin: 10px 0;
        height: auto;
    }

    .categories .row .col:nth-child(4) img {
        margin: 10px 0;
    }
}


@media screen and (min-width:768px) and (max-width:991px) {
    .col {
        width: 50%;
        padding: 0 20px;
    }

    .categories img {
        width: 100%;
        margin: 10px 0;
        height: auto;
    }

    .categories .row .col:nth-child(4) img {
        margin: 10px 0;
    }
}


@media screen and (min-width:992px) and (max-width:1440px) {
    .col {
        width: 40%;
        margin: 0 10px;
    }

    .categories img {
        width: 100%;
        margin: 10px 0;

    }

    .categories .row .col:nth-child(4) img {
        margin: 10px 0;
    }
}

