@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');

.container {
  margin: 50px 0 100px 5%;
  font-family: 'Noto Sans Mongolian', sans-serif;
  font-style: italic;
}

.title {
  font-weight: 400;
  color: #353535;
  font-size: 24px;
  letter-spacing: 1px;
  margin: 30px 0;
}

.col {
  font-family: 'Noto Sans Mongolian', sans-serif;
  text-align: center;
  margin: 0 20px;
  cursor: pointer;
}

.col:first-child {
  margin-left: 0;
}

.product img {
  width: 100%;
}

.col p {
  margin: 15px 0 5px 0;
}

.col .itemName {
  font-weight: 600;
  padding: 0 10px;
}

.col .itemPrice {
  color: rgb(154, 154, 154);
  margin: 0;
}