@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');

.category {
    padding: 0 20px;

}

.item {
    cursor: pointer;
    margin: 20px;
    text-align: center;
    animation: items;
    animation-duration: 2s;
    animation-iteration-count: 1;
}


.item img {
    width: 100%;
}

.item p {
    font-family: 'Noto Sans Mongolian', sans-serif;
    margin: 5px 0;
}

.item .itemName {
    font-weight: 600;
    font-family: 'Noto Sans Mongolian', sans-serif;
}

.item .itemPrice {
    color: rgb(154, 154, 154);
}

.pagination {
    text-align: right;
    margin: 30px 30px 0 0;
}

.inputSearch {
    font-family: 'Noto Sans Mongolian', sans-serif;
}

.inputSearch::placeholder {
    font-family: 'Noto Sans Mongolian', sans-serif;
    color: gray;
}

/* Style: effect */
.item:hover img {
    transition: 0.3s all ease;
    box-shadow: 2px 2px 2px 2px rgb(205, 205, 205);
}

@keyframes items {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}