@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');

.container {
    font-family: 'Noto Sans Mongolian', sans-serif;
    font-style: italic;
}

.container .name {
    margin: 0;
    font-style: italic;
}

.container .price {
    color: gray;
    font-weight: 400;
    font-style: normal;
    font-size: 20px;
    margin: 10px 0 30px 0;
}

.container .description {
    color: gray;
    font-style: italic;
    text-align: justify;
    margin: 20px 0 40px 0;
    font-size: 16px;
    line-height: 30px;
}

.category {
    font-weight: 700;
    letter-spacing: 1px;
    margin-bottom: 20px;
}

.category span {
    margin-left: 3px;
    color: gray;
    font-weight: 500;
    letter-spacing: 0;
}

.form {
    display: flex;
    border: 1px solid rgb(176, 176, 176);
    width: 350px;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}

.form b {
    margin-left: 10px;
    color: rgb(176, 176, 176);
    font-weight: 400;
    letter-spacing: 1px;
}

.amountItem {
    margin-left: 15%;
    line-height: 0;
}

.amountItem span {
    font-size: 18px;
}

.btnAdd {
    cursor: pointer;
    border: 1px solid #353535;
    border-radius: 0;
    color: white;
    background-color: #353535;
    font-style: italic;
    padding: 15px 25px;
    line-height: 10px;
    font-family: 'Noto Sans Mongolian', sans-serif;
}

.icon {
    margin: 0 5px;
    cursor: pointer;
    font-size: 18px;
}

/* Style: effect */
.btnAdd:hover {
    transition: 0.5s all ease;
    border: 1px solid rgb(37, 157, 232);
    background-color: rgb(37, 157, 232);
}

.icon:focus {
    color: rgb(37, 157, 232);
}