@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');

/* Style: gernal */
.container {
  background-color: #f8f9fa;
  padding: 50px 80px;
  text-align: center;
}

.row {
  width: 1140px;
  margin: 0 auto;
}


.row .col h2,
.row .col p {
  font-weight: 500;
  font-family: 'Playfair Display', serif;
  font-style: italic;
  margin: 5px 0;
  letter-spacing: 1px;
}

.row .col p {
  color: #b1b1b1;
  letter-spacing: 0
}

/* ------------------ */
.section {
  margin: 50px 0;
  padding: 0 80px;
}

.section input {
  border-radius: 0;
  font-weight: 600;
  font-family: 'Playfair Display', serif;
}

.section input::placeholder {
  font-weight: 400;
  font-size: 14px;
  font-family: 'Playfair Display', serif;
  color: rgb(38, 38, 39);
}

.section button {
  border-radius: 0;
  height: 50px;
  background-color: #383838;
}

/* style: Effect */
.inputEmail:hover {
  border: 1px solid #383838;
}

.inputEmail:focus {
  border: 1px solid rgb(23, 117, 218);
  outline: 0;
  box-shadow: 0 0 0 0 #383838;
}

@media screen and (min-width:320px) and (max-width:575px) {
  .container {
    padding: 10px;
  }

  .row {
    width: 100%;
  }

  .col {
    margin: 20px 0;
  }

  .section {
    width: 100%;
    padding: 0;
  }

  .row {
    justify-content: center;
    text-align: center;
  }
}


@media screen and (min-width:576px) and (max-width:767px) {
  .container {
    padding: 50px 10px;
  }

  .container .row {
    width: 100%;
  }

  .container .row .col h2 {
    font-size: 18px;
  }

  .container .row .col p {
    font-size: 12px;
  }

  .section .row {
    width: 100%;
    padding: 0;
  }
}


@media screen and (min-width:768px) and (max-width:991px) {
  .container {
    padding: 50px 10px;
  }

  .container .row {
    width: 100%;
  }

  .section {
    padding: 10px;
  }

  .section .row {
    justify-content: space-around;
    width: 100%;

  }
}