@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');

.title {
    font-family: 'Noto Serif Old Uyghur', serif;
    font-style: italic;
}

.textBoxMessage {
    width: 100%;
    transform: translate(0%, 100%);
    text-align: center;
    margin-bottom: 10%;
}

.textBoxMessage .messageLogin,
.textBoxMessage span {
    font-family: 'Roboto Slab', serif;
    font-size: 24px;
}

.textBoxMessage .iconLogin {
    margin-right: 10px;
}

/* ------------------------------------------------ */

.listTitle,
.transferPage {
    background-color: #F8F9FA;
    font-style: italic;
    margin: 30px 0;
    padding: 20px;

}

.listTitle .col p {
    font-family: 'Noto Sans Mongolian', sans-serif;
    font-weight: 600;
    text-align: center;
    font-size: 18px;
    color: rgb(66, 66, 66);
}

.listTitle .col .titlePrice {
    padding-left: 30%;
}

.listTitle .col .titleQuantity {
    padding-left: 25%;
}

.listTitle .col .titleTotal {
    padding-left: 30%;
}

/* ----------------------------------- */

.infoItem {
    margin: 30px 0;
    align-items: center;
    text-align: center;

}

.infoItem img {
    width: 120px;
    height: 120px;
}

.infoItem .infoName {
    font-weight: 600;
    color: rgb(59, 59, 59);
    font-size: 16px;
    font-family: 'Noto Sans Mongolian', sans-serif;
}

.infoItem .infoPrice,
.infoItem .infoPrice~span {
    color: gray;
    font-family: 'Noto Sans Mongolian', sans-serif;
    margin: 0 !important;
}

.infoItem .infoPrice {
    margin-bottom: 5px;
    font-family: 'Noto Sans Mongolian', sans-serif;
}

.amountItem .amount {
    margin: 0 10px;
    font-size: 16px;
    font-family: 'Noto Sans Mongolian', sans-serif;
}

.iconDecrease,
.iconIncrease {
    font-size: 16px;
    font-family: 'Noto Sans Mongolian', sans-serif;
}

.iconDelete {
    cursor: pointer;
    transition: 1s all ease;
    font-size: 20px;
    margin-right: 20px;
}

/* ------------------------------------------- */
.transferPage {
    padding: 10px 20px;
    align-items: center;
}

.transferPage .col:nth-child(2) {
    text-align: right;
}

.transferPage p {
    display: inline-block;
}

.transferPage .shopping {
    cursor: pointer;
    margin-left: 20px;
    color: gray;
    font-size: 18px;
}

.transferPage .iconBackPage,
.transferPage .iconNextPage {
    font-size: 18px;
    color: gray;
}


.transferPage .btnTransferPage {
    background-color: transparent;
    padding: 10px 20px;
    border: 0;
}

.btnTransferPage .checkout {
    font-size: 18px;
    color: gray;
    margin-right: 10px;
}

/* Style: Effect CSS */
.iconDelete:hover {
    transform: rotate(360deg);
    font-size: 24px;

}

.iconDecrease:hover,
.iconIncrease:hover {
    color: rgb(61, 61, 241);
    transition: 0.5s all ease;
}

.transferPage .shopping:hover,
.transferPage .iconBackPage:hover,
.transferPage .iconBackPage:hover~.shopping {
    color: rgb(68, 68, 68);
    transition: 0.5s all ease;
    cursor: pointer;
}

.transferPage .checkout:hover,
.transferPage .iconNextPage:hover,
.transferPage .checkout:hover~.iconNextPage {
    color: rgb(68, 68, 68);
    transition: 0.5s all ease;
    cursor: pointer;
}

.textBoxMessage .iconLogin:hover {
    color: rgb(22, 147, 242);
    transition: 0.3s all ease-in-out;
}