@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Old+Uyghur&display=swap');

.container {
    position: relative;
}

.input {
    position: relative;
    border-radius: 0;
    border: none;
    border-bottom: 2px solid gray;
    width: 61%;
    padding: 15px 15px 15px 30px;
    margin: 5px 0;
    color: #494949;
    font-weight: 600;
    font-family: 'Noto Sans Mongolian', sans-serif;
}

.input~.focusBorder {
    position: absolute;
    bottom: 5px;
    left: 15%;
    width: 0;
    height: 2px;
    background-color: #3fbef5;
    transition: 0.8s all ease-in-out;
}

.errorMessage {
    display: none;
}

.errorMessageInvalid {
    text-align: left;
    color: red;
    font-size: 12px;
    margin: 3px 0 0 15%;
}

/* Style: effect */

.input::placeholder {
    color: #807f7f;
    font-weight: 600;
    font-family: 'Noto Serif Old Uyghur', serif;
}

.input:focus {
    outline: 0;
}

.input:focus~.focusBorder {
    width: 70%;
}