@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Old+Uyghur&display=swap');

.menu {
    width: 100%;

}

ul.ant-menu.ant-menu-root.ant-menu-inline.ant-menu-light.menu.css-dev-only-do-not-override-3mqfnx {
    border: 0;
}

.ant-menu-submenu-title {
    display: block !important;
    font-style: italic;
    border-radius: 0px !important;
    font-family: 'Noto Serif Old Uyghur', serif;
    font-weight: 600;
    background-color: rgb(98, 98, 98);
    width: 100% !important;
    margin: 0 !important;
}

:where(.css-dev-only-do-not-override-3mqfnx).ant-menu-light .ant-menu-submenu-selected>.ant-menu-submenu-title,
:where(.css-dev-only-do-not-override-3mqfnx).ant-menu-light>.ant-menu .ant-menu-submenu-selected>.ant-menu-submenu-title {
    color: white;
    border-radius: 2px !important;
}


li.ant-menu-item.ant-menu-item-only-child {
    font-family: 'Noto Serif Old Uyghur', serif;
    font-style: italic;
    color: gray !important;
    margin: 0;
    padding: 0 25px !important;
    width: 100%;
    background-color: white !important;
}

.ant-menu-submenu-title span.ant-menu-title-content {
    color: white;
}

:where(.css-dev-only-do-not-override-3mqfnx).ant-menu-inline .ant-menu-sub.ant-menu-inline>.ant-menu-submenu>.ant-menu-submenu-title {
    padding: 0 25px !important;
}

li.ant-menu-item.ant-menu-item-active.ant-menu-item-selected.ant-menu-item-only-child {
    background-color: rgba(125, 125, 125, 0.2) !important;
}

:where(.css-dev-only-do-not-override-3mqfnx).ant-menu-light .ant-menu-submenu-selected>.ant-menu-submenu-title {
    border-radius: 0 !important;
}

.ant-menu-submenu-arrow {
    color: white !important;
}

/* Style: effect  */

.ant-menu-submenu-title:hover {
    background-color: rgb(62, 62, 62) !important;
}

li.ant-menu-item.ant-menu-item-only-child:hover {
    border-radius: 0;
}

.ant-menu-submenu-selected {
    border-radius: 0 !important;
}