@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Mongolian&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Old+Uyghur&display=swap');

.billCart {
    background-color: #F8F9FA;
    font-style: italic;
    margin-top: 65px;
}

.container {
    width: 70%;
    margin: 0 auto;
    padding: 30px 0;
}

.title {
    font-family: 'Noto Serif Old Uyghur', serif;
    margin: 0;
}

.subtotal {
    border-bottom: 1px solid black;
    padding-bottom: 10px;
}

.subtotal p {
    font-family: 'Noto Sans Mongolian', sans-serif !important;
    font-size: 20px;
    color: rgb(78, 77, 77);
    font-weight: 700;
    margin: 10px 0;
}

.subtotal p.price {
    color: rgb(155, 155, 155) !important;
    text-align: right;
    font-weight: 500;
}

/* -------------------------------------- */

.total {

    align-items: center;
}

.total p {
    font-family: 'Noto Sans Mongolian', sans-serif !important;
    font-size: 20px;
    color: rgb(78, 77, 77);
    font-weight: 700;
    margin: 10px 0;
}

.total p.price {
    color: rgb(91, 91, 91) !important;
    text-align: right;
    font-weight: 500;
    font-size: 24px;
}

/* ---------------------------------------- */
.inputCoupon {
    margin-top: 30px;
    width: 93.2%;
    padding: 15px 10px;
}

.inputCoupon::placeholder {

    color: gray;
    font-style: italic;
}

.inputCoupon:focus {
    outline: 1px solid rgb(32, 204, 107);

}

.addCoupon {
    width: 100%;
    padding: 15px 50px;
    color: white;
    background-color: #383838;
    font-size: 16px;
}

.addCoupon:hover {
    transition: 0.5s all ease;
    background-color: rgb(32, 204, 107);
    border: 1px solid rgb(32, 204, 107);
}

.iconGift {
    font-size: 20px;
    margin-right: 5px;
}